import { Menu } from '@mantine/core';
import { ReactNode } from 'react';
import { shareLink, shareWall } from 'src/utils/vk/sharing-method';
import { ArticleType } from '../../hooks/useCurrentNewsData';
import { formatDateUnix } from 'src/utils/date';
import { APP_URL } from 'src/utils/constants';

type Props = {
  children: ReactNode;
  data: ArticleType;
};

export const SharingMenu = ({ children, data }: Props) => {
  const newsText = `
: ${data.author}
 : ${formatDateUnix(data.date)}
: ${data.title}
: 
${data.text ?? ''}

${data.url ? `: ${data.url}` : ''}
`;

  const link = `${APP_URL}#${data.id}`;

  const handleClick = (type: string) => {
    switch (type) {
      case 'wall':
        shareWall(newsText, link);
        break;
      case 'send':
        shareLink(link);
        break;
      default:
        break;
    }
  };

  return (
    <Menu>
      <Menu.Target>{children}</Menu.Target>

      <Menu.Dropdown>
        <Menu.Item onClick={() => handleClick('wall')}>  </Menu.Item>
        <Menu.Item onClick={() => handleClick('send')}> </Menu.Item>
      </Menu.Dropdown>
    </Menu>
  );
};
